.LightsOut-board {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}

.neon-shadow {
  border-radius: 1px;
  box-shadow: 20px 20px 50px #5545af, -30px -30px 60px #ff008c,
    10px -10px 30px rgba(49, 3, 255, 0.637),
    -10px 10px 30px rgba(241, 0, 0, 0.349);
}

.LightsOut-note {
    color: #fff;
    font-size: 1rem;
}

.LightsOut-title {
    text-align: center;
    font-size: 3.3em;
    margin-bottom: 0;
    margin-top: 0;
    line-height: 1;
    color: #fff;
    padding-top: 20px;
    font-family: Monoton;
    animation: neon1 1.5s ease-in-out infinite alternate;
}

@keyframes neon1 {
    from {
      text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #1194ff,
        0 0 70px #0a86eb, 0 0 80px #115cff, 0 0 100px #2911ff, 0 0 150px #6411ff;
    }
    to {
      text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #004bbb,
        0 0 35px #6411ff, 0 0 40px #8011ff, 0 0 50px #4f0272, 0 0 75px #4c0cb3;
    }
  }

  
  @font-face {
    font-family: neon;
    src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
  }
  
  .neon-orange {
    font-family: neon;
    color: #FB4264;
    font-size: 3.3rem;
    text-shadow: 0 0 3vw #F40A35;
    animation: neon-orange 1s ease infinite;
    -moz-animation: neon-orange 1s ease infinite;
    -webkit-animation: neon-orange 1s ease infinite;
  }
  
  .neon-blue {
    font-family: neon;
    color: #426DFB;
    font-size: 0.9rem;
    text-shadow: 0 0 3vw #2356FF;
    animation: neon-blue 2s linear infinite;
    -moz-animation: neon-blue 2s linear infinite;
    -webkit-animation: neon-blue 2s linear infinite;
    -o-animation: neon-blue 2s linear infinite;
  }
  
  @keyframes neon-orange {
    0%,
    100% {
      text-shadow: 0 0 1vw #FA1C16, 0 0 3vw #FA1C16, 0 0 10vw #FA1C16, 0 0 10vw #FA1C16, 0 0 .4vw #FED128, .5vw .5vw .1vw #806914;
      color: #FED128;
    }
    50% {
      text-shadow: 0 0 .5vw #800E0B, 0 0 1.5vw #800E0B, 0 0 5vw #800E0B, 0 0 5vw #800E0B, 0 0 .2vw #800E0B, .5vw .5vw .1vw #40340A;
      color: #806914;
    }
  }
  
  
  @keyframes neon-blue {
    0%,
    100% {
      text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 .4vw #8BFDFE, .5vw .5vw .1vw #147280;
      color: #28D7FE;
    }
    50% {
      text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
      color: #146C80;
    }
  }
  